import {API} from '../../services/services.api'

const ON_URL_CHANGE = 'ON_URL_CHANGE'
const ON_ANSWER_CHANGE = 'ON_ANSWER_CHANGE'
const ADD_SURVEY_FORM = 'ADD_SURVEY_FORM'
const initialState = {
    surveys: {
        s1: {
            s_url: '',
            s_right_answer: ''
        }
    },
    winners: []
}

const GET_POLL_BY_ID = 'GET_POLL_BY_ID'

export default (state = initialState, action) => {
    const {type, payload} = action
    switch (type) {
        case ON_ANSWER_CHANGE: {
            const {s_key, value} = payload
            return {
                ...state,
                surveys: {
                    ...state.surveys,
                    [s_key]: {
                        ...state.surveys[s_key],
                        s_right_answer: value
                    }
                }
            }
        }
        case ON_URL_CHANGE: {
            const {s_key, value} = payload
            return {
                ...state,
                surveys: {
                    ...state.surveys,
                    [s_key]: {
                        ...state.surveys[s_key],
                        s_url: value
                    }
                }
            }
        }
        case ADD_SURVEY_FORM: {
            let surveysLength = Object.keys(state).length
            surveysLength === 0 ? (surveysLength += 2) : (surveysLength += 1)
            const surveyID = 's' + surveysLength
            return {
                ...state,
                surveys: {
                    ...state.surveys,
                    [surveyID]: {s_url: '', s_right_answer: ''}
                }
            }
        }
        case GET_POLL_BY_ID: {
            const pollData = payload
        }
        default:
            return state
    }
}

function getRightAnswerID({response, s_right_answer}) {
    const rightAnswer = response.answers[s_right_answer - 1]
    console.log('RIGHT ANSWER:', rightAnswer)
    return rightAnswer.id
}

